@use "global" as *;

.mv {
	margin-inline:auto;
	width:100%;
  position: relative;
}
.mv__img {
  margin-inline: auto;
  width: 100%;
}
.mv__img img {
	height:rem(460);
  aspect-ratio: 375 / 460;
  object-fit: cover;
  @include mq(md) {
		height:rem(548);
    aspect-ratio: 1440 / 548;
  }
}
.mv__title {
  position: absolute;
  top: 56%;
  left: 50%;
  transform: translate(-50%, -60%);
  color: #fff;
	width: max-content;
  z-index: 2;
  text-align: center;
  font-size: rem(32);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: 1.5;
  letter-spacing: 0;
	white-space: nowrap;

  @include mq(md) {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -60%);
    font-size: rem(40);
  }
}
.mv__title.mv__title--black {
	display: inline-block; 
  background: rgba(10, 10, 30, 0.5);
  padding: 0.2em 0.5em; // 文字の周りに余白
  border-radius: 4px;
	white-space: nowrap;
}
.mv__title::first-letter {
  text-transform: uppercase;
}

.mv__video-container {
	position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh; /* 画面いっぱいに表示 */
  overflow: hidden;
}

.mv__video-container video {
  width: 100%; /* 動画の横幅を100%に設定 */
  height: 100%; /* 動画の高さも100%に設定 */
  object-fit: cover; /* 動画を要素全体にフィットさせる */
  object-position: center; /* 中央揃えで表示 */
}
